.about {
  width: 100%;
  height: max-content;
  padding: 148px;
  margin: auto;
  text-align: center;
  max-width: 1045px;

  @include onMobile {
    padding: 120px 42px;
  }

  &__title {
    @extend %big-title;
    color: $dark-blue;
  }

  &__text {
    @extend %text;
    color: $blue-grey;
  }
}
