.testimonials {
  padding: 0 0 148px;

  @include onMobile {
    padding: 0 0 120px;
  }

  &__container {
    @extend %container;
  }

  &__name {
    @extend %small-text;
    color: $light-grey;
  }

  &__title {
    @extend %big-title;
    display: flex;
    align-items: center;
    color: $dark-blue;
    margin: 16px 0 85px;
  }
}
